@import '../../../common/styles/sass/mixins';

.friendListContainer {
    width: 1000px;
    position: relative;

    .toPacksList {
        position: absolute;
        left: 20px;
        top: -10px;

        &:before {
            content: '';
            position: absolute;
            left: -20px;
            bottom: 3px;
            width: 14px;
            height: 12px;
            background-image: url('../../../common/assets/image/backProfile.svg');
            background-repeat: no-repeat;
        }
    }

    .headBlock {
        @include flex(row, center, center, wrap);
        //border: 1px solid blue;
        margin-bottom: 20px;
        margin-top: 20px;
        justify-content: space-between;

        .headName {
            @include text(22px, 27px, 600);
            @include flex(row, center, center, wrap);
            .menuIcon {
                margin-top: 5px;
                margin-left: 5px;
                width: 20px;
                height: 20px;
                border: 1px solid black;
                border-radius: 100%;
                cursor: pointer;
                position: relative;
                .nav {
                    position: absolute;
                    bottom: 45px;
                    right: 118px;
                    opacity: 0;
                    transition: all 0.8s ease 0s;
                    z-index: 10;
                }
                .active {
                    position: absolute;
                    bottom: 45px;
                    right: 118px;
                    opacity: 1;
                    transition: all 0.8s ease 0s;
                    z-index: 10;
                }
            }
        }

        .button {
            width: 175px;
            height: 36px;
            border-radius: 30px;
            //background: #366eff;
            text-transform: capitalize;
            box-shadow: 0px 4px 18px rgba(54, 110, 255, 0.35), inset 0px 1px 0px rgba(255, 255, 255, 0.3);
        }
    }

    .descriptionBlock {
        //border: 1px solid #282c34;
        @include flex(row, start, center, wrap);
        @include text(14px, 17px, 500);

        .second {
            padding-left: 390px;
        }

        .third {
            padding-left: 120px;
        }
    }

    .settingsBlock {
        //border: 1px solid #525252;
        @include flex(row, start, center, wrap);
        margin-bottom: 24px;
        margin-top: 8px;
    }

    .tableBlock {
        //border: 1px solid #61dafb;
        margin-bottom: 36px;
    }

    .paginationBlock {
        border: 1px solid #282c34;
    }
}
